@charset "UTF-8";
/**
 * img 组件样式
 */
:root {
  --z: z;
  --z-color-blue-lighter: #bbdefb;
  --z-color-blue-light: #6ec6ff;
  --z-color-blue: #2196f3;
  --z-color-blue-dark: #0069c0;
  --z-color-blue-darker: #002171;
  --z-color-danger-lighter: #ffcdd2;
  --z-color-danger-light: #ff7961;
  --z-color-danger: #f44336;
  --z-color-danger-dark: #ba000d;
  --z-color-danger-darker: #7f0000;
  --z-color-success-lighter: #c8e6c9;
  --z-color-success-light: #80e27e;
  --z-color-success: #4caf50;
  --z-color-success-dark: #087f23;
  --z-color-success-darker: #003300;
  --z-color-warning-lighter: #fff9c4;
  --z-color-warning-light: #ffff72;
  --z-color-warning: #ffeb3b;
  --z-color-warning-dark: #c8b900;
  --z-color-warning-darker: #c6a700;
  --z-color-orange-lighter: #ffe0b2;
  --z-color-orange-light: #ff8a50;
  --z-color-orange: #ff5722;
  --z-color-orange-dark: #c41c00;
  --z-color-orange-darker: #870000;
  --z-color-light-lighter: #fff;
  --z-color-light-light: #fff;
  --z-color-light: #f5f5f5;
  --z-color-light-dark: #e0e0e0;
  --z-color-light-darker: #d4d4d4;
  --z-color-dark-lighter: #9e9e9e;
  --z-color-dark-light: #6d6d6d;
  --z-color-dark: #424242;
  --z-color-dark-dark: #1b1b1b;
  --z-color-dark-darker: #000;
  --z-color-white-lighter: #fff;
  --z-color-white-light: #fff;
  --z-color-white: #fff;
  --z-color-white-dark: #e0e0e0;
  --z-color-white-darker: #d4d4d4;
  --z-color-black-lighter: #9e9e9e;
  --z-color-black-light: #6d6d6d;
  --z-color-black: #000;
  --z-color-black-dark: #000;
  --z-color-black-darker: #000;
  --z-color-grey-lighter: #eee;
  --z-color-grey-light: #cfcfcf;
  --z-color-grey: #9e9e9e;
  --z-color-grey-dark: #707070;
  --z-color-grey-darker: #333333;
  --z-color-primary-lighter: #bbdefb;
  --z-color-primary-light: #6ec6ff;
  --z-color-primary: #2196f3;
  --z-color-primary-dark: #0069c0;
  --z-color-primary-darker: #002171;
  --z-color-secondary-lighter: #bbdefb;
  --z-color-secondary-light: #6ec6ff;
  --z-color-secondary: #2196f3;
  --z-color-secondary-dark: #0069c0;
  --z-color-secondary-darker: #002171;
  --z-color-text-light-primary: #fff;
  --z-color-text-light-secondary: rgba(255, 255, 255, 0.7);
  --z-color-text-light-hint: rgba(255, 255, 255, 0.5);
  --z-color-text-light-divider: rgba(255, 255, 255, 0.12);
  --z-color-text-dark-primary: rgba(0, 0, 0, 0.87);
  --z-color-text-dark-secondary: rgba(0, 0, 0, 0.54);
  --z-color-text-dark-hint: rgba(0, 0, 0, 0.38);
  --z-color-text-dark-divider: rgba(0, 0, 0, 0.12);
  --z-color-bg: #f5f5f5;
  --z-color-link: #2196f3;
  --z-color-border: #d6d6d6;
  --z-color-border-light: #e5e5e5;
  --z-color-border-dark: #707070;
  --z-gap: 8px;
  --z-gap-half: 4px;
  --z-gap-double: 16px;
  --z-gap-triple: 24px;
  --z-gutter: 10px;
  --z-gutter-half: 5px;
  --z-gutter-double: 20px;
  --z-gutter-triple: 30px;
  --z-border-thin: 1px;
  --z-border-thick: 2px;
  --z-border: #d6d6d6 1px solid;
  --z-border-radius: 4px;
  --z-border-radius-m: 6px;
  --z-border-radius-l: 8px;
  --z-font-base: 14px;
  --z-h1: 28px;
  --z-h2: 24px;
  --z-h3: 18px;
  --z-h4: 16px;
  --z-h5: 14px;
  --z-h6: 12px;
  --z-z-index-lower: 998;
  --z-z-index: 999;
  --z-z-index-higher: 1000;
  --z-shadow-lower: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  --z-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
  --z-shadow-higher: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2); }

.z-img {
  display: block; }
  .z-img-content {
    width: 100%;
    vertical-align: middle; }
  .z-img-skeleton {
    background-color: var(--z-color-grey-light); }
